<template>
  <div class="pageContent">
    <!-- 数据列表 - 筛选-->
    <el-card class="operate-container mt10" shadow="never">
      <div class="pageHead flex-row-between">
        <div>月账单汇总</div>
        <el-tag type="success">{{form.balanceDay}}</el-tag>
      </div>
      <el-form ref="form" :model="form" label-width="140px">
        <div class="flex-row-around">
          <el-form-item style="width:50%;" label="店铺名称:">
            <div>{{shop.shopName}}</div>
          </el-form-item>
          <el-form-item style="width:50%;" label="币种:">
            <div>人民币</div>
          </el-form-item>
        </div>
        <div class="flex-row-around">
          <el-form-item style="width:50%;" label="起始日期:">
            <div>{{dateForm.startDateEn}}</div>
          </el-form-item>
          <el-form-item style="width:50%;" label="结束日期:">
            <div>{{dateForm.endDateEn}}</div>
          </el-form-item>
        </div>
      </el-form>
    </el-card>
    <!-- 数据列表 -->
    <el-card class="mt10 filter-container" shadow="never">
      <div class="pageHead">店铺余额</div>
      <el-table :data="[form]" style="width: 100%">
        <el-table-column prop="lastBalanceAmount" label="期初余额"></el-table-column>
        <el-table-column prop="incomeAmount" label="本期入账">
          <template slot-scope="scope">
            <div class="succeseFont">+{{scope.row.incomeAmount}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="expenditureAmount" label="本期出账">
          <template slot-scope="scope">
            <div class="dangerFont">-{{scope.row.expenditureAmount}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="thisBalanceAmount" label="期末结余"></el-table-column>
      </el-table>
      <div class="mt40">
        <div class="w5">
          <div class="list infoFont flex-row-start">
            <div class="f3">交易手续费</div>
            <div class="f1">
              <div>{{form.orderTradeCount}}笔</div>
              <div>-￥{{form.orderTradeAmount}}</div>
            </div>
            <el-button @click="goToInfo(21,'out',form.orderTradeCount,form.orderTradeAmount)" class="f1" type="text" :disabled="form.orderTradeCount == 0">明细</el-button>
          </div>
          <div class="list infoFont flex-row-start">
            <div class="f3">佣金</div>
            <div class="f1">
              <div>{{form.commissionCount}}笔</div>
              <div>-￥{{form.commissionAmount}}</div>
            </div>
            <el-button @click="goToInfo(22,'out',form.commissionCount,form.commissionAmount)" class="f1" type="text" :disabled="form.commissionCount == 0">明细</el-button>
          </div>
          <div class="list infoFont flex-row-start">
            <div class="f3">提现</div>
            <div class="f1">
              <div>{{form.withdrawCount}}笔</div>
              <div>-￥{{form.withdrawAmount}}</div>
            </div>
            <el-button @click="goToInfo(25,'out',form.withdrawCount,form.withdrawAmount)"  class="f1" type="text" :disabled="form.withdrawCount == 0">明细</el-button>
          </div>
          <div class="list infoFont flex-row-start">
            <div class="f3">交易退款</div>
            <div class="f1">
              <div>{{form.refundCount}}笔</div>
              <div>-￥{{form.refundAmount}}</div>
            </div>
            <el-button @click="goToInfo(24,'out',form.refundCount,form.refundAmount)" class="f1" type="text" :disabled="form.refundCount == 0">明细</el-button>
          </div>
          <div class="list infoFont flex-row-start">
            <div class="f3">提现手续费</div>
            <div class="f1">
              <div>{{form.withdrawTradeCount}}笔</div>
              <div>-￥{{form.withdrawTradeAmount}}</div>
            </div>
            <el-button @click="goToInfo(23,'out',form.withdrawTradeCount,form.withdrawTradeAmount)" class="f1" type="text" :disabled="form.withdrawTradeAmount == 0">明细</el-button>
          </div>
          <div class="list infoFont flex-row-start">
            <div class="f3">邀请奖励</div>
            <div class="f1">
              <div>{{form.inviteCount}}笔</div>
              <div>-￥{{form.inviteAmount}}</div>
            </div>
            <el-button @click="goToInfo(26,'out',form.inviteCount,form.inviteAmount)" class="f1" type="text" :disabled="form.inviteCount == 0">明细</el-button>
          </div>
          <div class="mt20 zc warningFont">
            <div>合计：共支出{{form.expenditureCount}}笔</div>
            <div class="mt10 f20">￥{{form.expenditureAmount}}</div>
          </div>
        </div>
        <div class="w5">
          <div class="list infoFont flex-row-start">
            <div class="f3">交易收入</div>
            <div class="f1">
              <div>{{form.orderCount}}笔</div>
              <div>+￥{{form.orderAmount}}</div>
            </div>
            <el-button @click="goToInfo(1,'in',form.orderCount,form.orderAmount)" class="f1" type="text" :disabled="form.orderCount == 0">明细</el-button>
          </div>
          <div class="list infoFont flex-row-start">
            <div class="f3">交易手续费退回</div>
            <div class="f1">
              <div>{{form.refundTradeCount}}笔</div>
              <div>+￥{{form.refundTradeAmount}}</div>
            </div>
            <el-button @click="goToInfo(2,'in',form.refundTradeCount,form.refundTradeAmount)" class="f1" type="text" :disabled="form.refundTradeCount == 0">明细</el-button>
          </div>
          <div class="list infoFont flex-row-start">
            <div class="f3">充值</div>
            <div class="f1">
              <div>{{form.rechargeCount}}笔</div>
              <div>+￥{{form.rechargeAmount}}</div>
            </div>
            <el-button @click="goToInfo(3,'in',form.rechargeCount,form.rechargeAmount)" class="f1" type="text" :disabled="form.rechargeCount == 0">明细</el-button>
          </div>
          <div class="list infoFont flex-row-start">
            <div class="f3">佣金退回</div>
            <div class="f1">
              <div>{{form.refundCommissionCount}}笔</div>
              <div>+￥{{form.refundCommissionAmount}}</div>
            </div>
            <el-button @click="goToInfo(4,'in',form.refundCommissionCount,form.refundCommissionAmount)" class="f1" type="text" :disabled="form.refundCommissionCount == 0">明细</el-button>
          </div>
          <div class="list infoFont flex-row-start">
            <div class="f3">邀请奖励退回</div>
            <div class="f1">
              <div>{{form.refundInviteCount}}笔</div>
              <div>+￥{{form.refundInviteAmount}}</div>
            </div>
            <el-button @click="goToInfo(5,'in',form.refundInviteCount,form.refundInviteAmount)" class="f1" type="text" :disabled="form.refundInviteCount == 0">明细</el-button>
          </div>
          <div class="list infoFont flex-row-start">
            <div class="f3">会员卡收入</div>
            <div class="f1">
              <div>{{form.vipRechargeCount}}笔</div>
              <div>+￥{{form.vipRechargeAmount}}</div>
            </div>
            <el-button @click="goToInfo(6,'in',form.vipRechargeCount,form.vipRechargeAmount)" class="f1" type="text" :disabled="form.vipRechargeCount == 0">明细</el-button>
          </div>
           <div class="mt20 zc warningFont">
            <div>合计：共收入{{form.incomeCount}}笔</div>
            <div class="mt10 f20">￥{{form.incomeAmount}}</div>
          </div>
        </div>
      </div>
      <div class="mt20 flex-row-center">
        <el-button type="primary" @click="exportDetail">打印汇总账单</el-button>
      </div>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopAssetMonthTotal , getMonthDay } from '@/api/assetsManage/capitalManage'
export default {
  data(){
    return{
      form:{},
      dateForm:{}
    }
  },
  created(){
    this.getData()
  },
  methods:{
    getData(){
      getShopAssetMonthTotal(this.$route.query.id).then(res=>{
        console.log('余额月账单汇总',res)
        if(res.code == 200){
          this.form = res.data;
          getMonthDay(res.data.balanceDay).then(res1=>{
            console.log('获取日期',res1)
            if(res.code ==200){
              this.dateForm = res1.data
            }
          })
        }
      })
    },
    change(){
      this.$forceUpdate()
    },
    goToInfo(e,t,n1,n2){
      this.$router.push({
        path:'/assetsManage/capitalManage/detailInfo',
        query:{
          tradeType:e,
          date:this.form.balanceDay,
          dateType:'month',
          startDate:this.dateForm.startDate,
          endDate:this.dateForm.endDate,
          type:t,
          count:n1,
          amount:n2
        }
      })
    },
    exportDetail(){
      let str = `shopId=${this.shop.shopId}&dataStr=${this.form.balanceDay}`
      window.open(
        `/sys/shopAssetMonth/exportAssetMonthTotal?${str}`
      )
    }
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .w5{
    width:48%;
    font-size: 14px;
    text-align: center;
  }
  .list{
    border-bottom: 1px solid #909399;
  }
  .f3{
    flex: 3;
  }
  .f1{
    flex:1
  }
  .mt40{
    margin-top: 40px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
  }
  .zc{
    text-align: right;
    font-size: 16px;
    font-weight: bold;
  }
</style>